<template lang="html">
  <div class="container-fluid" style="background:rgba(248,248,248,1);min-height:100vh;">
    <div class="yuang_ttto">
        <p></p>
    </div>
      <div class="container gongh_form">
          <div class="x_qy_gl">
            <!-- <p></p> -->
          </div>
          <van-form @submit="onSubmit">
            <van-field
              v-model="company"
              name=""
              label=""
              placeholder=""
              disabled

            />
            <!-- <van-field
              v-model="codenumber"
              name=""
              :class="{active:iscodeShow}"
              label=""
              placeholder=""
              @change="shurucode()"
                autocomplete="off"
            /> -->
            <!-- <van-field
              v-model="username"
              :class="{actives:isnameShow}"
              name=""
              label=""
              placeholder=""
              autocomplete="off"
              v-on:focus="shuru()"
              ref="input2"
            />
             -->
            <van-field
              v-model="codenumber"
              name=""
              label=""
              placeholder=""
              @change="shurucode()"
                autocomplete="off"

            />

              <van-field
                v-model="username"
                name=""
                label=""
                placeholder=""
                autocomplete="off"
                v-on:focus="shuru()"
                ref="input2"
              />

            <!-- 
              <div class="code_ghao" :class="{actives:activeopi}" v-show="namevalue">
                <ul>
                  <li @click="addvalue(index)" v-for="(item,index) in namelist" :key="index" :value="item.name" v-text="item.name"></li>
                 </ul>
              </div> -->

              <!-- @change="shuruname()" -->

              <div v-if="this.username == '' ||  this.codenumber == '' "  class="gonghao_tj "   >
                <van-button round block type="info" native-type="submit">
                  
                </van-button>
              </div>
              <div v-else  class="gonghao_tj activeopi"   @click="comenum_tijiao()">
                <van-button round block type="info" native-type="submit">
                  
                </van-button>
              </div>



            </van-form>
          <div class="zhengce_chek">
            <van-checkbox v-model="checked" shape="square"> <i @click="xieyi"></i>  <i @click="user_mi"></i> </van-checkbox>
          </div>
      </div>

      <!-- // -->
      <div class="user_pop">
          <van-popup v-model="show_xieyi"
          closeable
          close-icon="close"
          >
            <div class="xieyi_content">
              {{this.zhengce}}
            </div>
          </van-popup>
      </div>
      <div class="user_pop">
          <van-popup v-model="show_user" closeable
          close-icon="close">
            <div class="user_content">
              {{this.user}}
            </div>
        </van-popup>
      </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      bianse:0,
      searchVal: "",
      company: '',
      codenumber: '',
      username:'',
      company_id:'',
      checked: false,
      iscodeShow:false,
      isnameShow:false,
      namelist:[],
      namevalue:true,


      zhengce:[],
      user:[],
      show_xieyi:false,
      show_user:false,
      //
      isactiveopi:false,

    };
  },
  mounted() {
    // let params = this.$route.params;
    // let company_id = params.company_id;
  },
  computed:{

  },
  created(){
      this.get_Company_data();
      // this.get_user_shenfen();
  },
  watch:{

     username:function(){
        this.shuru();
     },
     codenumber:function(){
        this.gonghaoshuru();
     },

   },
  methods: {
    //
   user_mi() {
     this.show_user = true;
   },
    xieyi() {
     this.show_xieyi = true;
   },
    //input
    addvalue(index){
      this.username=this.namelist[index].name;
      this.namevalue=false;

    },
    //
    gonghaoshuru(){
      this.codenumber=this.codenumber.replace(/[\u4e00-\u9fa5]/ig,'')

    },
    shuru(){
      console.log(111111);
      var $this=this;
      var params={
        company_id:this.$route.query.company_id,
        query:this.username,
        page:1,
        page_size:5
      }
      var qs = require('qs');
      var parm = JSON.stringify(params);
      let url = this.api.userApi.hedui_shenfen
      this.axios.post(url+'/'+params.company_id+'/'+params.query+'/'+params.page+'/'+params.page_size,)
      .then((res) => {
        console.log(res.data.data.company_user_list)
        this.namelist =  res.data.data.company_user_list;
        // this.namevalue=true

      }).catch((error) => {
        console.warn(error)
      })
    },

    shurucode(){
      if (this.codenumber == '' || this.codenumber == undefined || this.codenumber == null){
    //alert("");
        this.iscodeShow=true
        this.$toast("");
      }else {
        this.iscodeShow=false
      }
    },
    shuruname(){
      if (this.username == '' || this.username == undefined || this.username == null){
    //alert("");
        this.isnameShow=true
        this.$toast("");
      }else {
        this.isnameShow=false
      }
    },
    onSubmit(values) {
      console.log('submit', values);
    },
    get_Company_data(){

        // 

      var  Params={
        company_id:this.$route.query.company_id,
        privacy:"1",
        user:"1",
      }
     // console.log(Params.company_id);
      var qs = require('qs');
      let url = this.api.userApi.get_Company
      this.axios.post(url,qs.stringify(Params),)
        .then((res) => {

          console.log(res);
          // console.log(res.data.data);

          if (res.data.code==200) {
            this.company=res.data.data.company_name;
            //
            this.zhengce=res.data.data.protocal.privacy.content;
            console.log(this.zhengce);
            //
            this.user=res.data.data.protocal.user.content;
          }
          else{
            this.$toast(res.data.msg);
          }
        }).catch((error) => {

      })




    },

    comenum_tijiao(){




      //
      // if( this.username == "" && this.codenumber == ""){
      //   this.$toast("!");
      // }
      // else if(this.username == ""){
      //   this.$toast("!");
      // }
      // else if(this.codenumber == ""){
      //   this.$toast("");
      // }
      // else if(this.checked==false){
      //   this.$toast("!");
      // }
      //
      // else



      let url = this.api.userApi.get_checkou_Company
      var  Params={
        company_id:this.$route.query.company_id,
        worker_id:this.codenumber,
        name:this.username,
      }
      var qs = require('qs');
      this.axios.post(url,qs.stringify(Params),).then((res) => {

          if(res.data.code==200){


            if(this.checked==false){
              this.$toast("!");
            }
            else{

              console.log(res.data.data.companyuser);
              this.company_id = res.data.data.companyuser.company_id;
              this.name = res.data.data.companyuser.name;
              this.worker_id = res.data.data.companyuser.worker_id;
              this.mobile = res.data.data.companyuser.mobile;
              localStorage.setItem("company_id", this.$route.query.company_id);
              console.log(this.company_id);
              console.log(this.name);
              console.log(this.worker_id);
              console.log(this.mobile);
              console.log(res);


              this.$toast("");
                setTimeout(()=>{
                  this.$router.push({
                    path:'/logincode',
                    name:'logincode',
                    params:{
                      companyuser_id:res.data.data.companyuser.id,
                      mobile:res.data.data.companyuser.mobile,
                    }
                  })
                },1500)

            }

          }
          else{

            if( this.username == "" && this.codenumber == ""){
              this.$toast("!");
            }
            else if(this.username == ""){
              this.$toast("!");
            }
            else if(this.codenumber == ""){
              this.$toast("");
            }
            else if(this.checked==false){
              this.$toast("!");
            }
            else{
              this.$toast(res.data.msg);
            }



          }
      })


    }


  },
};

</script>

<style lang="css" scoped>
</style>



